/* src/components/common/sidebar.css */
.relaxplus-sidebar {
  background: #f5f0e8 !important;
  border-right: 1px solid #e8ddd4 !important;
  height: 100vh !important;
  position: fixed !important;
  left: 0 !important;
  top: 0 !important;
  z-index: 100 !important;
  display: flex !important;
  flex-direction: column !important;
  box-shadow: 2px 0 8px rgba(93, 64, 55, 0.1) !important;
}

/* Logo Section */
.sidebar-logo {
  padding: 32px 24px;
  text-align: center;
  border-bottom: 1px solid #e8ddd4;
  background: #f5f0e8;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.logo-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-svg {
  width: 60px;
  height: 60px;
  transition: all 0.3s ease;
}

.relaxplus-sidebar.ant-layout-sider-collapsed .logo-svg {
  width: 40px;
  height: 40px;
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  color: #5d4037;
  font-family: "Georgia", serif;
  letter-spacing: -0.5px;
  margin: 0;
  transition: all 0.3s ease;
}

/* Menu Section */
.sidebar-menu {
  flex: 1;
  padding: 24px 0;
  overflow-y: auto;
}

.relaxplus-menu {
  background: transparent !important;
  border: none !important;
  padding: 0 16px;
}

.relaxplus-menu .ant-menu-item {
  background: transparent !important;
  border: none !important;
  margin: 8px 0 !important;
  border-radius: 12px !important;
  height: 48px !important;
  line-height: 48px !important;
  padding: 0 20px !important;
  display: flex !important;
  align-items: center !important;
  transition: all 0.3s ease !important;
  color: #5d4037 !important;
  font-weight: 500 !important;
  font-size: 16px !important;
}

.relaxplus-menu .ant-menu-item:hover {
  background: rgba(93, 64, 55, 0.08) !important;
  color: #5d4037 !important;
}

.relaxplus-menu .ant-menu-item-selected {
  background: #5d4037 !important;
  color: #ffffff !important;
  box-shadow: 0 2px 8px rgba(93, 64, 55, 0.3) !important;
}

.relaxplus-menu .ant-menu-item-selected::after {
  display: none !important;
}

.relaxplus-menu .ant-menu-item .anticon {
  font-size: 18px !important;
  margin-right: 12px !important;
  transition: all 0.3s ease !important;
}

.relaxplus-menu .ant-menu-item-selected .anticon {
  color: #ffffff !important;
}

/* Collapsed state */
.relaxplus-sidebar.ant-layout-sider-collapsed .relaxplus-menu .ant-menu-item {
  padding: 0 !important;
  justify-content: center !important;
}

.relaxplus-sidebar.ant-layout-sider-collapsed
  .relaxplus-menu
  .ant-menu-item
  .anticon {
  margin-right: 0 !important;
  font-size: 20px !important;
}

/* Collapse Button */
.sidebar-collapse-btn {
  position: absolute;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  width: 36px;
  height: 36px;
  background: #5d4037;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(93, 64, 55, 0.3);
}

.sidebar-collapse-btn:hover {
  background: #4a2c20;
  transform: translateX(-50%) scale(1.1);
}

.sidebar-collapse-btn .anticon {
  color: #ffffff;
  font-size: 14px;
}
